.main__container {
  position: relative;
  width: 85rem;
  padding: 55px 70px 0;
}

.resp_decor {
  display: none;
}

.main__header {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: baseline;
  background-color: #fbebea;
}

.header__title {
  font-size: 27px;
  line-height: 32px;
  font-family: 'PlayfairDisplay';
  font-weight: bold;
  margin: 0px 47px 0 0;
}

.main_nav_burger-button {
  display: none;
}

.main__header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 91%;
}

.nav-link {
  text-decoration: none;
  color: var(--primary-text);
  font-size: 13px;
  font-family: 'Circe';
}

.nav-link:hover {
  color: var(--secondary-text);
}

.nav-link:focus {
  color: var(--secondary-text);
  outline: none;
}

.telephone-number {
  font-size: 14px;
  font-weight: bold;
  font-family: 'Circe';
  margin-left: 200px;
}

.strawbarry_decorations {
  max-width: 100%;
  position: absolute;
  top: 90px;
  left: 24%;
  z-index: -1;
}

.main__cake-image {
  max-width: 100%;
  position: absolute;
  top: 104px;
  left: 41%;
  z-index: -1;
}

.main__content-container {
  padding: 0;
}

.main__title--text {
  font-size: 96px;
  line-height: 67px;
  font-family: 'PlayfairDisplay';
  font-weight: bold;
  margin: 152px auto 0;
}

.main__hand-made--text {
  font-size: 41px;
  font-family: 'IntroScriptR';
  margin: 20px auto 0;
}

.main__button {
  width: 230px;
  height: 56px;
  background-color: var(--secondary-text);
  color: var(--general-white);
  font-family: 'Circe';
  font-weight: normal;
  font-size: 13px;
  border: hidden;
  border-radius: 30px;
  transition: 100ms;
  margin: 50px auto 0;
}

.main__button:hover {
  box-shadow: 0 5px 20px var(--secondary-text);
}

.main__button:focus {
  box-shadow: 0 5px 20px var(--secondary-text);
  outline: none;
}

.main__info {
  width: 41%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 80px 0 0;
}

.svg_item {
  width: 53px;
  fill: var(--secondary-text);
}

.main__info--text {
  font-family: 'Circe';
  font-weight: bold;
  font-size: 14px;
  margin-right: 35px;
}

/* design for tablets */
@media only screen and (max-width:768px) {
  .main__container {
    width: 86rem;
    padding: 42px 80px 0;
  }

  .resp_decor {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    background-color: var(--general-white);
    height: 105px;
  }

  .main__header{
    background-color: var(--general-white);
  }

  .header__title {
    font-size: 42px;
  }

  .main_nav_burger-button {
    display: inline-block;
  }

  .main__header__nav {
    align-items: self-end;
  }

  .resp-nav-link {
    visibility: hidden;
  }

  .telephone-number {
    font-size: 27px;
  }

  .strawbarry_decorations {
    top: 136px;
    left: 27%;
  }

  .main__cake-image {
    top: 137px;
    left: 43%;
  }

  .main__title--text {
    font-size: 132px;
    line-height: 91px;
    margin: 222px auto 0;
  }

  .main__hand-made--text{
    font-size: 65px;
    margin: 42px auto 0;
  }

  .main__button {
    width: 410px;
    height: 91px;
    font-size: 24px;
    border-radius: 50px;
    margin: 84px auto 0;
  }

  .main__info {
    width: 75%;
    margin: 242px 0 0;
  }

  .svg_item {
    width: 100px;
  }

  .main__info--text {
    font-size: 27px;
    margin-right: 0px;
  }

}